@import-normalize; /* bring in normalize.css styles */

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Montserrat';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:
    source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
button {
  cursor: pointer;
  border: none;
  background: none;
}
img {
  display: block;
  max-width: 100%;
}
input {
  outline: none;
  border: none;
  background-color: #e4e4e4;
}

.container {
  max-width: 293px;
  width: 100%;
  margin: 0 auto;
  /* padding-left: 50px;
  padding-right: 50px; */
  @media screen and (min-width: 768px) {
    /* padding-left: 100px;
    padding-right: 100px; */
    max-width: 634px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 1140px;
    /* padding-left: 150px;
  padding-right: 150px; */
  }
}

.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-bottom: 40px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 384px;
  height: 211px;
}

.custom-slide img {
  width: 384px;
  height: 211px;
  object-fit: cover;
  border-radius: 12px;
}
